"use client";

import "./details.css";
import { useEffect, useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { toast } from "react-hot-toast";

// Location APIs
// import { getCountries, getStates, getCities } from "@/lib/location.api";

// Routes
import {
  LABEL_ORG_STEP_CATEGORY,
  LABEL_ORG_STEP_DETAILS,
  LABEL_ORG_STEP_ACCOUNT,
  SUBTITLE_ORG_DETAILS,
  BTN_CONTINUE,
  LABEL_ORG_COUNTRY,
  LABEL_ORG_STATE,
  LABEL_ORG_CITY,
  LABEL_ORG_NAME,
  LABEL_ORG_SELECT_COUNTRY,
  LABEL_ORG_SELECT_STATE,
  LABEL_ORG_SELECT_CITY,
  LABEL_LOADING,
  LABEL_LOADING_STATES,
  LABEL_LOADING_CITIES,
  MSG_ERR_FILL_ALL_FIELDS,
} from "../../../../../const-value/config-message/page";

export default function OrganizerDetailsPage() {
  const router = useRouter();
  const params = useSearchParams();

  const category = params.get("cat");

  // ---------------- STATE ----------------
  const [country, setCountry] = useState("");
  const [stateName, setStateName] = useState("");
  const [city, setCity] = useState("");
  const [orgName, setOrgName] = useState("");

  const [countries, setCountries] = useState([]);
  const [states, setStates] = useState([]);
  const [cities, setCities] = useState([]);

  const [loadingCountry, setLoadingCountry] = useState(false);
  const [loadingState, setLoadingState] = useState(false);
  const [loadingCity, setLoadingCity] = useState(false);

  // ---------------- LOAD COUNTRIES ----------------
  useEffect(() => {
    async function loadCountries() {
      setLoadingCountry(true);
      const data = await getCou();
      setCountries(data || []);
      setLoadingCountry(false);
    }
    loadCountries();
  }, []);

  // ---------------- LOAD STATES ----------------
  useEffect(() => {
    if (!country) return;

    async function loadStates() {
      setLoadingState(true);
      const data = await getStates(country);
      setStates(data || []);
      setLoadingState(false);
    }
    loadStates();
  }, [country]);

  // ---------------- LOAD CITIES ----------------
  useEffect(() => {
    if (!stateName) return;

    async function loadCities() {
      setLoadingCity(true);
      const data = await getCities(country, stateName);
      setCities(data || []);
      setLoadingCity(false);
    }
    loadCities();
  }, [stateName]);

  // ---------------- CONTINUE ----------------
  function onContinue(e) {
    e.preventDefault();

    if (!country || !stateName || !city || !orgName) {
      return toast.error(MSG_ERR_FILL_ALL_FIELDS);
    }

    const qs = new URLSearchParams({
      cat: category,
      country,
      state: stateName,
      city,
      orgName,
    }).toString();

    router.push(`${"/auth/organization/signup/account"}?${qs}`);
  }

  return (
    <div className="org-details-shell">
      {/* STEPPER */}
      <div className="org-stepper">
        <div className="step active">
          1<br />
          <small>{LABEL_ORG_STEP_CATEGORY}</small>
        </div>
        <div className="step active">
          2<br />
          <small>{LABEL_ORG_STEP_DETAILS}</small>
        </div>
        <div className="step">
          3<br />
          <small>{LABEL_ORG_STEP_ACCOUNT}</small>
        </div>
      </div>

      <h1>{LABEL_ORG_STEP_DETAILS}</h1>
      <p className="subtitle">{SUBTITLE_ORG_DETAILS}</p>

      {/* FORM */}
      <form className="details-form" onSubmit={onContinue}>
        {/* COUNTRY */}
        <label>{LABEL_ORG_COUNTRY}</label>
        <select value={country} onChange={(e) => setCountry(e.target.value)}>
          <option value="">
            {loadingCountry ? LABEL_LOADING : LABEL_ORG_SELECT_COUNTRY}
          </option>
          {countries.map((c) => (
            <option key={c.name} value={c.name}>
              {c.name}
            </option>
          ))}
        </select>

        {/* STATE */}
        <label>{LABEL_ORG_STATE}</label>
        <select
          value={stateName}
          onChange={(e) => setStateName(e.target.value)}
        >
          <option value="">
            {loadingState ? LABEL_LOADING_STATES : LABEL_ORG_SELECT_STATE}
          </option>
          {states.map((s) => (
            <option key={s.name} value={s.name}>
              {s.name}
            </option>
          ))}
        </select>

        {/* CITY */}
        <label>{LABEL_ORG_CITY}</label>
        <select value={city} onChange={(e) => setCity(e.target.value)}>
          <option value="">
            {loadingCity ? LABEL_LOADING_CITIES : LABEL_ORG_SELECT_CITY}
          </option>
          {cities.map((ct) => (
            <option key={ct.name} value={ct.name}>
              {ct.name}
            </option>
          ))}
        </select>

        {/* ORG NAME */}
        <label>{LABEL_ORG_NAME}</label>
        <input
          type="text"
          placeholder="Enter organization name"
          value={orgName}
          onChange={(e) => setOrgName(e.target.value)}
        />

        <button type="submit" className="continue-btn">
          {BTN_CONTINUE}
        </button>
      </form>
    </div>
  );
}
